  background-size: cover;
  text-align: center;
 
}
.h1-slider-caption {
    position: absolute;
    top: 35vh;
    left: 30vw;
}
.h1-slider-caption h1 {
  font-family: "Farro", sans-serif;
  font-size: 75px;
